  transition: visibility 0s linear 0.1s, opacity 0.3s ease;
}

.modal.open {
  visibility: visible;
  opacity: 1;
  transition-delay: 0s;
}

.modal__overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 21;
  background-color: rgba(0, 0, 0, 0.808);
}

.modal__close {
  position: absolute;
  top: 10px;
  right: 15px;
  border: none;
  outline: none;
  background: none;
  font-size: 28px;
  color: #ffffff;
  font-weight: bold;
  cursor: pointer;
  z-index: 999;
}

.modal__close:hover {
  color: #000;
}

.modal__container {
  position: relative;
  z-index: 999 !important;
  width: auto;
  height: auto;
  max-width: 990px;
  max-height: 95vh;
  width: 90%;
  top: 50%;
  transform: translateY(-50%);
  box-shadow: 0 0 10px 1px #000000a6;
  margin: 0 auto;
  padding: 30px;
  overflow-y: auto;
  background: radial-gradient(#c57136, #381f08);
  text-align: center;
  border-radius: 20px;
  filter: url("#noise2");
}

