@import "./helpers/fonts.scss";
@import "./helpers/normalize.scss";
@import "./helpers/smart-grid.scss";
@import "./helpers/variables.scss";
@import "./helpers/mixins.scss";
@import "../modules/_header/header.scss";
@import "../modules/_sidebar/sidebar.scss";
@import "../modules/_footer/footer.scss";

* {
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  width: 100vw;
  height: 100vh;
  background-color: #201d20;
  position: relative;
  overflow-x: hidden;
  font-family: "Roboto", sans-serif;
}

svg {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
}
.firstLayer,
.secondLayer,
.thirdLayer {
  opacity: 0;
  svg {
    height: 100% !important;
  }
  &__path {
    height: auto !important;
  }
}

.firstLayer__svg {
  //   top: 20px;
  //   left: 200px;
  height: auto !important;
}

.secondLayer__svg {
  //   top: 300px;
  //   left: 800px;
  height: auto !important;
}

.thirdLayer__svg {
  //   bottom: 100px;
  //   right: 100px;
  height: 100px !important;
  image-rendering: pixelated;
}

.btn {
  background-color: #201d20;
  color: white;
  border-radius: 50px;
  font-size: 18px;
  display: inline-block;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color 0.4s ease;

  &:hover {
    background-color: black;
  }
}

.hero {
  position: relative;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  z-index: 2;

  &__btn {
    font-weight: bold;
    width: 180px;
    height: 50px;
  }

  &__wrapper {
    color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    width: 100vw;
    position: relative;
    height: 100vh;
    //z-index: 1000;
  }

  &__title {
    font-size: 78px;
    margin-bottom: 10px;
    max-width: 70%;
    width: 100%;
    color: white;
    font-family: "Dancing Script", cursive;
    text-shadow: 2px 2px 3px rgba(51, 28, 13, 0.473);
    z-index: 8;
    filter: url("#noise2");

    @media only screen and (max-width: 900px) {
      font-size: 50px;
    }

    @media only screen and (max-width: 400px) {
      font-size: 40px;
    }
    @media only screen and (max-height: 550px) {
      font-size: 40px;
    }
  }

  &__subtitle {
    font-size: 20px;
    width: 600px;
    margin-bottom: 70px;
    max-width: 70%;
    width: 100%;
    color: white;
    text-shadow: 0px 0px 2px white;
    font-family: "Dancing Script", cursive;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.473);
    z-index: 8;
    @media only screen and (max-width: 700px) {
      font-size: 18px;
    }
    @media only screen and (max-width: 400px) {
      font-size: 16px;
    }

    @media only screen and (max-height: 720px) {
      margin-bottom: 20px;
    }
  }

  &__img {
    max-width: 550px;
    position: absolute;
    bottom: -5%;
    left: 6%;
    z-index: 7;
    transition: transform 0.15s ease !important;
    @media only screen and (max-height: 900px) {
      max-width: 450px;
    }
    @media only screen and (max-height: 720px) {
      max-width: 350px;
    }

    @media only screen and (max-height: 600px) {
      max-width: 260px !important;
    }

    @media only screen and (max-width: 700px) {
      width: 450px;
    }

    @media only screen and (max-width: 550px) {
      width: 350px;
    }

    @media only screen and (max-width: 450px) {
      width: 260px;
    }
  }
}

.cover {
  background-image: url("../img/bg.jpg");
  position: absolute;
  width: 100vw;
  height: 100%;
  filter: url("#noise");
  background-size: cover;
  background-repeat: repeat-y;
  z-index: -100;
  @media only screen and (max-width: 1921px) {
    background-size: auto;
  }
}

.main {
  position: relative;
  width: 100vw;
  height: auto;
  align-items: center;
  justify-content: center;

  &-contact {
    // margin-bottom: 1111px;
  }

  // background-color: #201d20;

  &__wrap {
    display: flex;
    justify-content: center;
    z-index: 12;
    @media only screen and (max-width: 1400px) {
      flex-direction: column;
    }
  }

  &__content {
    padding-top: 16%;
    color: white;
    position: relative;
    height: auto;
    padding-bottom: 350px;
    @media only screen and (max-width: 700px) {
      padding-bottom: 250px;
    }

    @media only screen and (max-width: 550px) {
      padding-bottom: 200px;
    }
  }

  &__title {
    font-size: 78px;
    margin-bottom: 10px;
    max-width: 50%;
    width: 100%;
    position: absolute;
    left: 30%;
    top: 5%;
    text-shadow: 0px 0px 5px rgba(255, 255, 255, 0.473);
    @media only screen and (max-width: 1650px) {
      font-size: 77px;
    }
    @media only screen and (min-width: 2300px) {
      top: 10%;
    }
    @media only screen and (min-width: 3100px) {
      top: 10%;
      left: 33%;
    }
    @media only screen and (min-width: 3600px) {
      top: 13%;
      left: 33%;
    }
    @media only screen and (max-width: 1600px) {
      font-size: 76px;
    }
    @media only screen and (max-width: 1550px) {
      font-size: 75px;
    }
    @media only screen and (max-width: 1500px) {
      //  left: 26%;
    }

    @media only screen and (max-width: 1400px) {
      display: none;
    }
  }

  &__subtitle {
    font-size: 18px;
    width: 600px;
    margin-bottom: 50px;
    max-width: 70%;
    width: 100%;
  }
}

.svgAfter,
.svgFooter {
  position: relative;
  top: -100px;
  z-index: 0;
  svg {
    height: auto !important;
    width: auto !important;
  }
  &__path {
    height: auto !important;
  }
}

.svgAfter {
  bottom: 0;
  @media only screen and (max-width: 1900px) {
    top: -80px;
  }

  @media only screen and (max-width: 1500px) {
    top: -60px;
  }
  @media only screen and (max-width: 1200px) {
    top: -50px;
  }
  @media only screen and (max-width: 1100px) {
    top: -40px;
  }
  @media only screen and (max-width: 800px) {
    top: -30px;
  }

  @media only screen and (max-width: 700px) {
    top: -20px;
  }
  @media only screen and (max-width: 370px) {
    top: -10px;
  }
}

.svgFooter {
  // margin-top: 500px;
  position: absolute;
  top: 100%;
  margin-top: 200px;
  transform: rotate(180deg);
  z-index: -1;
  svg {
    height: auto !important;
    width: auto !important;
    //max-height: 1080px;
    //z-index: -1000;
  }
  &__path {
    height: auto !important;
  }
  @media only screen and (max-width: 2250px) {
    margin-top: 180px;
  }

  @media only screen and (max-width: 1950px) {
    margin-top: 160px;
  }

  @media only screen and (max-width: 1700px) {
    margin-top: 140px;
  }

  @media only screen and (max-width: 1450px) {
    margin-top: 120px;
  }
  @media only screen and (max-width: 1250px) {
    margin-top: 100px;
  }

  @media only screen and (max-width: 900px) {
    margin-top: 80px;
  }
  @media only screen and (max-width: 600px) {
    margin-top: 60px;
  }
  @media only screen and (max-width: 350px) {
    margin-top: 70px;
  }
}

.title {
  &-liquid {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 72px;
    color: white;
    z-index: -1;
    text-shadow: 0px 0px 5px rgba(255, 255, 255, 0.815);
    font-family: "Dancing Script", cursive;
    @media only screen and (max-width: 700px) {
      font-size: 40px;
    }
  }
}

.circle {
  height: 400px;
  width: 400px;
  background-color: rgb(255, 255, 255);
  border-radius: 50%;
  position: absolute;
  display: block;
  z-index: 2;

  transition: transform 0.2s ease !important;
  //filter: url("#noise2");
  // filter: url("#metal");

  box-shadow: inset 30px 20px 50px 1px rgba(114, 86, 68, 0.9);
  &:after {
    content: "";

    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    opacity: 1;
    border-radius: 50%;
    box-shadow: 20px 20px 30px 0px rgba(27, 12, 3, 0.699);
  }
  &-1 {
    left: 10px;
    top: -20%;

    background: radial-gradient(#52330b, #000000);
    // display: none;

    &-c {
      top: 60% !important;
      left: 50px;
    }
  }
  &-2 {
    height: 600px;
    width: 600px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    //background-color: #e4c09d;

    background: radial-gradient(#52330b, #000000);

    .circle-2-line {
      path {
        stroke: #00000041;
      }
    }

    //  background: radial-gradient(rgb(255, 255, 255), #ecd3b9);
  }
  &-3 {
    right: 10px;
    top: -22%;
    background: linear-gradient(to right, #a57b5d, #ffc7a1);
    background: radial-gradient(#fcbc28, #9a6615);
    background: radial-gradient(#52330b, #000000);
    // display: none;

    //background: linear-gradient(to right, rgb(255, 255, 255), #f3d4b4);
    &-c {
      top: 55% !important;
      right: 20px;
      //left: 50px;
    }
  }

  &-4 {
    right: 6%;
    top: -15%;

    background: radial-gradient(#52330b, #000000);
    // display: none;

    //background: linear-gradient(to right, rgb(255, 255, 255), #f3d4b4);
  }
  &-5 {
    left: 8%;
    top: -10%;

    background: radial-gradient(#52330b, #000000);
    // display: none;

    //background: linear-gradient(to right, rgb(255, 255, 255), #f3d4b4);
  }

  &-6,
  &-7,
  &-8,
  &-9 {
    display: none;
  }

  @media only screen and (max-width: 1500px) {
    &-1,
    &-3,
    &-4,
    &-5 {
      display: none;
    }
  }

  @media only screen and (max-height: 800px) {
    &-1,
    &-3,
    &-4,
    &-5 {
      display: none;
    }
  }

  @media only screen and (max-height: 900px) {
    &-2 {
      max-height: 500px;
      max-width: 500px;
    }
  }
  @media only screen and (max-height: 720px) {
    &-2 {
      max-height: 400px;
      max-width: 400px;
    }
  }

  @media only screen and (max-height: 600px) {
    &-2 {
      max-height: 290px !important;
      max-width: 290px !important;
    }
  }

  @media only screen and (max-width: 700px) {
    &-2 {
      height: 500px;
      width: 500px;
    }
  }

  @media only screen and (max-width: 550px) {
    &-2 {
      height: 400px;
      width: 400px;
    }
  }

  @media only screen and (max-width: 450px) {
    &-2 {
      height: 290px;
      width: 290px;
    }
  }

  @media only screen and (min-width: 2500px) {
    &-6,
    &-7 {
      display: block;
    }

    &-6 {
      right: 19%;
      top: 22%;

      background: radial-gradient(#52330b, #000000);
      // display: none;

      //background: linear-gradient(to right, rgb(255, 255, 255), #f3d4b4);
    }
    &-7 {
      left: 18%;
      top: 30%;

      background: radial-gradient(#52330b, #000000);
      // display: none;

      //background: linear-gradient(to right, rgb(255, 255, 255), #f3d4b4);
    }
  }

  @media only screen and (min-width: 3100px) {
    &-8,
    &-9 {
      display: block;
    }

    &-8 {
      right: 22%;
      top: 62%;

      background: radial-gradient(#52330b, #000000);
      // display: none;

      //background: linear-gradient(to right, rgb(255, 255, 255), #f3d4b4);
    }
    &-9 {
      left: 27%;
      top: 70%;

      background: radial-gradient(#52330b, #000000);
      // display: none;

      //background: linear-gradient(to right, rgb(255, 255, 255), #f3d4b4);
    }
  }
}

