.items {
  @include row-flex();

  justify-content: space-around;
  max-width: 1200px;
  margin: 0;
  z-index: 12;
  margin-top: 50px;
  position: relative;
  @media only screen and (max-width: 1400px) {
    margin: 0 auto;
  }
  @media only screen and (min-width: 2500px) {
    margin-bottom: 40px;
  }
  @media only screen and (min-width: 3000px) {
    margin-bottom: 90px;
  }
  @media only screen and (min-width: 3400px) {
    margin-bottom: 170px;
  }
  .item {
    @include col();
    @include size(3);
    @include size-md(5);
    @include size-xs(10);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    color: white;
    flex-basis: 0;
    z-index: 12;

    background-image: radial-gradient(#f19655, #5e4028);
    padding: 40px 10px;
    border-radius: 20px;
    min-width: 220px;
    text-align: center;
    // border-bottom-left-radius: 5px;
    //  border-bottom-right-radius: 5px;
    box-shadow: 0px 0px 15px 1px black;
    transition: all 0.5s ease;
    margin-bottom: 50px;
    position: relative;
    filter: url("#noise2");

    &:before {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 20px;
      z-index: 12;
      //border-bottom-left-radius: 5px;
      // border-bottom-right-radius: 5px;
      background-image: radial-gradient(#58390b, #000000);
      transition: opacity 0.4s ease;
      z-index: 0;
      opacity: 0;
    }

    &:hover:before {
      opacity: 1;
      //background-image: radial-gradient(#58410b, #000000);
    }

    &__img {
      max-width: 100px;
      z-index: 2 !important;
      opacity: 1 !important;
      align-self: center;
      margin-bottom: 10px;
    }
    &__title {
      text-shadow: 2px 2px 3px rgba(51, 28, 13, 0.986);
      z-index: 1;
      max-width: 90%;
      width: 100%;
      margin-bottom: 5px;
      // min-width: 10px;
    }
    &__price {
      max-width: 150px;
      z-index: 1;
      text-shadow: 2px 2px 3px rgba(51, 28, 13, 0.986);
    }
  }
}

.footer {
  height: auto;
  position: relative;
  width: auto;

  // background: linear-gradient(to left, #e29d6b, #815f43);
  background: linear-gradient(to left, #c57136, #381f08);
  background: linear-gradient(to left, #d6864c, #5e4028);
  filter: url("#noise2");

  // @media only screen and (max-width: 1400px) {
  text-align: center;
  //  }

  &__title {
    z-index: 9;
    position: relative;
    top: -250px;
    left: 11%;
    font-size: 58px;
    color: white;
    max-width: 50%;
    width: 100%;
    font-weight: bold;
    z-index: 15;
    cursor: pointer;
    text-shadow: 0px 0px 5px rgba(255, 255, 255, 0.473);

    &-c {
      visibility: hidden;
    }
    @media only screen and (max-width: 1400px) {
      font-size: 38px;
      left: 0%;
      top: -300px;
      transform: translate(-50%);
    }

    @media only screen and (min-width: 3100px) {
      top: -300px;
      left: 13%;
    }

    @media only screen and (max-width: 700px) {
      top: -220px;
    }
    @media only screen and (max-width: 550px) {
      top: -200px;
    }
  }

  &__text {
    z-index: 9;
    position: absolute;
    color: white;

    left: 50%;
    top: 10px;
    max-width: 90%;
    width: 100%;
    transform: translate(-50%, 0);
    text-shadow: 2px 2px 3px rgba(31, 16, 7, 0.87);

    @media only screen and (min-width: 1200px) {
      top: 20px;
    }
    @media only screen and (min-width: 1400px) {
      top: 40px;
    }
    @media only screen and (min-width: 1450px) {
      top: 60px;
    }
    @media only screen and (min-width: 1600px) {
      top: 80px;
    }

    @media only screen and (min-width: 2200px) {
      top: 100px;
    }
    @media only screen and (min-width: 3100px) {
      top: 130px;
    }

    @media only screen and (max-width: 600px) {
      font-size: 14px;
    }

    @media only screen and (max-width: 700px) {
      top: 20px;
    }

    &-c {
      top: -130px !important;
    }
  }
}

