//modal

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 999;
  padding: 30px;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  opacity: 0;
  visibility: hidden;
  transition: visibility 0s linear 0.1s, opacity 0.3s ease;

  &.open {
    visibility: visible;
    opacity: 1;
    transition-delay: 0s;
  }

  &__overlay {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 21;
    background-color: rgba(0, 0, 0, 0.808);
  }

  &__close {
    position: absolute;
    top: 10px;
    right: 15px;
    border: none;
    outline: none;
    background: none;
    font-size: 28px;
    color: #ffffff;
    font-weight: bold;
    cursor: pointer;
    z-index: 999;

    &:hover {
      color: #000;
    }
  }

  &__container {
    position: relative;
    z-index: 999 !important;
    width: auto;
    height: auto;
    max-width: 990px;
    max-height: 95vh;
    width: 90%;
    top: 50%;
    transform: translateY(-50%);
    box-shadow: 0 0 10px 1px #000000a6;
    margin: 0 auto;
    padding: 30px;
    overflow-y: auto;
    @media only screen and (max-width: 500px) {
      padding: 10px;
    }

    background: radial-gradient(#c57136, #381f08);
    text-align: center;
    border-radius: 20px;
    filter: url("#noise2");
    ///////////////
  }
}

.item-single {
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-shadow: 2px 2px 3px rgba(32, 18, 8, 0.664);
  position: relative;
  @media only screen and (max-width: 650px) {
    flex-direction: column;
    align-items: center;
    padding: 30px 10px;
  }

  &-cart {
    flex-direction: column;
    @media only screen and (max-width: 650px) {
      padding: 0px;
    }
  }

  &-c {
    margin-top: 350px;
    justify-content: center;
    max-width: 1400px;
    z-index: 12;

    @media only screen and (min-width: 2400px) {
      padding-top: 100px;
    }
    @media only screen and (min-width: 3100px) {
      padding-top: 220px;
    }

    @media only screen and (max-width: 1500px) {
      flex-direction: column;
      align-items: center;
      margin-top: 300px;
    }
    @media only screen and (max-width: 1400px) {
      margin-top: 280px;
    }
    @media only screen and (max-width: 1200px) {
      margin-top: 230px;
    }
    @media only screen and (max-width: 900px) {
      margin-top: 210px;
    }

    @media only screen and (max-width: 660px) {
      margin-top: 170px;
    }

    @media only screen and (max-width: 600px) {
      margin-top: 150px;
    }
    @media only screen and (max-width: 500px) {
      margin-top: 100px;
    }
  }

  &__p {
    max-width: 90%;
    font-family: "Dancing Script", cursive;
    text-align: center;
    text-shadow: 1px 1px 1px rgb(255, 255, 255);
    font-size: 20px;
    line-height: 25px;

    @media only screen and (max-width: 1500px) {
      max-width: 60%;
    }

    @media only screen and (max-width: 700px) {
      max-width: 90%;
      font-size: 18px;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-left: 10%;
    color: white;
    min-width: 250px;
    @media only screen and (max-width: 650px) {
      margin-left: 0%;
    }

    &-c {
      margin: 0;
    }
  }
  &__title {
    text-shadow: 2px 2px 3px rgba(0, 0, 0, 0.733);
    margin-bottom: 40px;
    font-size: 40px;

    @media only screen and (max-width: 800px) {
      font-size: 30px;
    }

    @media only screen and (max-width: 450px) {
      font-size: 25px;
    }
    @media only screen and (max-width: 1500px) {
      margin-bottom: 30px;
    }
  }
  &__price {
    margin-bottom: 20px;
    font-size: 30px;
    @media only screen and (max-width: 800px) {
      font-size: 20px;
    }

    &::before {
      content: "Price: ";
    }
  }
  &__label {
    font-size: 30px;
    width: 100%;
    @media only screen and (max-width: 800px) {
      font-size: 20px;
    }
  }

  picture {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__img {
    align-self: center;
    @media only screen and (max-width: 1000px) {
      max-width: 190px;
    }

    @media only screen and (max-width: 750px) {
      max-width: 150px;
    }
    @media only screen and (max-width: 650px) {
      max-width: 120px;
      margin-bottom: 30px;
    }

    @media only screen and (max-width: 450px) {
      max-width: 100px;
    }

    &-c {
      align-self: center;
      @media only screen and (max-width: 1500px) {
        margin-bottom: 50px;
      }
      @media only screen and (max-width: 1000px) {
        max-width: 90%;
      }
    }
  }

  &__quantity {
    background: transparent;
    color: white;
    border: none;
    border-bottom: 2px solid rgb(255, 255, 255);
    text-align: center;
    margin-bottom: 50px;
    font-size: 30px;
    max-width: 90px;
    box-shadow: 0px 0px 0px 0px black;
    text-shadow: 2px 2px 3px rgba(32, 18, 8, 0.664);
    @media only screen and (max-width: 800px) {
      font-size: 20px;
    }
  }
}

.btn {
  &-buy {
    padding: 10px 40px;
    font-size: 28px;
    min-width: 200px;
    min-height: 70px;
    background-color: rgba(0, 0, 0, 0.911);
    box-shadow: 2px 2px 5px 1px #0c0702;
    transition: all 0.3s ease;
    @media only screen and (max-width: 800px) {
      font-size: 25px;
    }

    &-cart {
      margin: 0 auto 10px;
    }
  }
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

.ofHidden {
  overflow: hidden;
}

.spanfix {
  display: none;
}

/////////// form

.contact {
  max-width: 600px;
  margin: 0 auto;
  color: white;
  font-family: "Dancing Script", cursive;
  margin-top: 100px;
  padding-bottom: 300px;
  z-index: 12;
  position: relative;
  width: 90%;
  @media only screen and (min-width: 2400px) {
    padding-bottom: 450px;
  }
  @media only screen and (min-width: 3100px) {
    padding-bottom: 550px;
  }
  @media only screen and (max-width: 800px) {
    padding-bottom: 200px;
  }

  @media only screen and (max-width: 400px) {
    padding-bottom: 150px;
  }

  &__form {
    z-index: 12;
  }
}

input {
  background: rgb(201, 201, 201);
  color: white;
  border: none;
  box-shadow: 0px 0px 20px 1px black;
  text-align: center;
  margin-bottom: 10px;
  font-size: 30px;
  text-shadow: 2px 2px 3px rgba(32, 18, 8, 0.664);
  width: 100%;
  color: black;
  padding: 5px 10px;

  filter: url("#noise2");

  @media only screen and (max-width: 800px) {
    font-size: 20px;
  }
  &::placeholder {
    color: rgb(133, 133, 133);
    text-shadow: none;
  }
  transition: all 0.3s ease;
  &:hover {
    opacity: 1;
  }
}

textarea {
  background: rgb(201, 201, 201);
  color: black;
  border: none;
  box-shadow: 0px 0px 20px 1px black;
  text-align: center;
  margin-bottom: 10px;
  padding: 5px 10px;
  font-size: 30px;
  text-shadow: 2px 2px 3px rgba(32, 18, 8, 0.664);
  width: 100%;
  filter: url("#noise2");

  transition: all 0.3s ease;
  @media only screen and (max-width: 800px) {
    font-size: 20px;
  }
  &::placeholder {
    color: rgb(133, 133, 133);
    text-shadow: none;
  }

  &:hover {
    opacity: 1;
  }
}

.label-c {
  position: relative;
  display: block;
  margin-bottom: 10px;
  font-size: 20px;
  margin-top: 10px;
}

.btn-c {
  cursor: pointer;
  margin-top: 10px;
  transition: all 0.3s ease;
  border-bottom: 0px solid #000;

  &:hover {
    color: white;
    background-color: rgb(0, 0, 0);
  }
}

html {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
::-webkit-scrollbar {
  width: 0px;
  background: transparent;
  // display: none;
}
::-webkit-scrollbar-thumb {
  background: #ff000000;
}

////////////////////@extend
//cart

table {
  border-collapse: collapse;
  width: 100%;
}

.table {
  margin: 50px 0px 30px;
  td,
  th {
    border: 2px solid #ffffff98;
    text-align: left;

    font-size: 18px;
    padding: 12px;
    // font-weight: bold;
    color: white;
    @media only screen and (max-width: 650px) {
      font-size: 16px;
      padding: 10px;
    }
    @media only screen and (max-width: 350px) {
      font-size: 15px;
      padding: 8px;
    }
  }

  th {
    font-size: 24px;
    color: white;
    background-color: rgba(0, 0, 0, 0.911);
    @media only screen and (max-width: 650px) {
      font-size: 18px;
    }
  }

  tr {
    background-color: rgba(0, 0, 0, 0.205);
  }

  tr:last-child {
    background-color: rgba(0, 0, 0, 0.911);
  }
  tr:last-child {
    td {
      font-size: 24px;
      font-weight: bold;
      @media only screen and (max-width: 650px) {
        font-size: 18px;
      }
    }
  }
}
