$navColor: transparent;
$logoColor: black;
$logoColor_hover: white;
$navItemColor: white;
$navItemColor_hover: black;
$burgerColor: black;
$MQ_menuColor: rgba(36, 47, 49, 0.465);
.header {
  width: 100%;
  height: auto;
  &-promo {
    position: relative;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.nav {
  display: flex;
  justify-content: space-around;
  width: 100%;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background-color: $navColor;
  z-index: 990;
  margin: 0 auto;
  //box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.5);
}

.logo {
  text-decoration: none;
  font-size: 40px;
  color: $logoColor;
  z-index: 99;
  align-items: center;
  transition: color 0.25s ease;
  position: relative;
  // background: linear-gradient(to bottom, #fcbc28, #9a6615);

  &__inner {
    background-image: url("../img/logo1-white.png");
    width: 70px;
    height: 70px;

    object-fit: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;

    &-white {
      background-image: url("../img/logo1-white.png");
    }
    @media only screen and (max-width: 650px) {
      width: 50px;
      height: 50px;
    }
  }

  @media screen and (max-width: 550px) {
    font-size: 30px;
  }
  &:hover {
    text-shadow: 0px 1px 1px black;
    color: $logoColor_hover;
  }
}

.navItems {
  display: flex;
  list-style: none;
  position: relative;
  padding: 0;
  &__item {
    &:not(:first-child) {
      margin-left: 30px;
    }
    &-inner {
      text-decoration: none;
      font-size: 36px;
      color: $navItemColor;
      transition: all 0.3s ease;
      text-shadow: 0px 1px 1px black;
      font-family: "Dancing Script", cursive !important;
      text-shadow: 1px 1px 5px rgba(255, 255, 255, 0.719);
      &:hover {
        color: #f19655;
        text-shadow: 1px 1px 5px #f19655;
      }
      @media only screen and (max-width: 600px) {
        font-size: 30px;
      }
    }
  }
}

.scrollLock {
  overflow: hidden;
}

.burger {
  display: block;
  position: relative;
  width: 35px;
  height: 25px;
  z-index: 999;
  margin: 0;
  cursor: pointer;
  &__1,
  &__2,
  &__3 {
    background-color: white;
    position: absolute;
    width: 100%;
    height: 3px;
    z-index: 99;
    left: 0;
    border-radius: 100px;
  }

  &__1 {
    top: 0;
    transition: all 0.35s ease;
  }

  &__2 {
    top: 11px;
    transition: all 0.2s ease;
  }

  &__3 {
    bottom: 0;
    transition: all 0.35s ease;
  }
}

.activeB1 {
  transform: rotate(45deg);
  top: 12px !important;
}

.activeB3 {
  transform: rotate(-45deg);
  bottom: 10px !important;
}

.activeB2 {
  transform: scale(0);
}

.logoCont {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: $navColor;
  padding: 7px 15px 0px 15px;
  z-index: 98;
  width: 100%;
  max-width: 1140px;
  margin-left: auto;
  margin-right: auto;
}

.inner {
  position: relative;
  max-width: 1140px;
  width: 100%;
  cursor: default;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: flex-end;
}

.navCont {
  position: fixed;
  top: 0%;
  right: -100%;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);

  padding-top: 40px;
  z-index: 10;
  display: flex;
  justify-content: end;
  transition: all 0.6s ease;
  overflow: auto;
  z-index: 9;
}

.active {
  right: 0%;
  padding-right: 10px;
}

.navItems {
  display: flex;
  flex-direction: column;
  margin: 0;
  align-items: flex-end;
  position: absolute;
  margin-top: 15px;
  &__item {
    margin-left: 0 !important;
    margin-top: 40px;
    transition: all 0.3s ease;
    &:hover {
      //position: relative;
      // top: -3px;
      // transform: scale(1.05);
    }
    &:last-child {
      margin-bottom: 40px;
    }
    &:first-child {
      margin-top: 45px;
    }
  }
}

.navColored {
  &:before {
    content: "";
    width: 100%;
    position: absolute;
    background: linear-gradient(to bottom, #fcbc28, #9a6615);
    // background: linear-gradient(to bottom, #d6864c, #5e4028);
    height: 100%;
    top: 0;
    left: 0;
    animation: navColored 0.3s ease;
    border-bottom-left-radius: 50px;
    border-bottom-right-radius: 50px;
  }
}

@keyframes navColored {
  from {
    background-color: rgba(33, 97, 134, 0);
    top: -50px;
  }
  to {
    background-color: rgba(68, 105, 126, 0.95);
    top: 0px;
  }
}

.burger-white {
  background-color: white !important;
}
