.irs {
  max-width: 300px;
  width: 100%;
  margin-right: 170px;

  @media only screen and (max-width: 1500px) {
    margin-right: 150px;
  }
  @media only screen and (max-width: 1400px) {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    align-items: center;
  }

  @media only screen and (max-width: 450px) {
    max-width: 280px;
  }
  &-from,
  &-to {
    box-shadow: 0px 0px 15px 1px black;
    padding: 1px;
    font-size: 12px !important;
    background-color: #6e4b2e !important;
    filter: url("#noise2");

    &:before {
      border-top-color: #5e4028 !important;
    }
  }

  &-bar {
    background-image: radial-gradient(#f19655, #5e4028);
    filter: url("#noise2");
    box-shadow: 0px 0px 15px 1px black;
  }

  &-single {
    padding: 1px;
    font-size: 12px !important;
    background-color: #6e4b2e !important;
    &:before {
      border-top-color: #5e4028 !important;
    }
  }

  &-min,
  &-max {
    font-size: 12px !important;
    //   visibility: hidden !important;
  }

  .from,
  .to {
    i {
      width: 18px !important;
      border-radius: 50%;
      margin-left: -9px !important;
      background-color: rgb(255, 255, 255) !important;
    }
  }
}
.sidebar {
  margin-left: 30px;
  z-index: 11;
  &__title {
    margin-bottom: 30px;
    font-size: 24px;
    text-shadow: 0px 0px 5px rgba(255, 255, 255, 0.473);
  }
  @media only screen and (max-width: 1400px) {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    align-items: center;
    min-width: 300px;
  }
  @media only screen and (max-width: 1900px) {
    margin-top: 50px;
  }
  @media only screen and (max-width: 550px) {
    margin-top: 50px;
  }
}

/////////////////@extend
//DROPDOWN

.dropbtn {
  background-image: radial-gradient(#f19655, #5e4028);
  color: white;
  padding: 16px;
  font-size: 22px;
  border: none;
  cursor: pointer;
  margin-top: 30px;
  min-width: 160px;
  border-radius: 0px;
  box-shadow: 0px 0px 15px 1px black;

  filter: url("#noise2");
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 10;

  background-image: radial-gradient(#f19655, #5e4028);
  color: white;
  font-size: 16px;
  filter: url("#noise2");

  border-radius: 10px;
  //box-shadow: 0px 0px 15px 1px black;

  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
}

.dropdown-content:hover ~ .dropbtn {
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
}

.dropdown-content a {
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  color: white;
}

.dropdown-content a:hover {
  background-image: radial-gradient(#58390b, #000000);
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown:hover .dropbtn {
  background-color: #3e8e41;
}

.checkboxWrap {
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 90px;
  @media only screen and (max-width: 1400px) {
    flex-direction: row;
    min-height: auto;
    margin-bottom: 70px;
  }
  @media only screen and (max-width: 380px) {
    flex-direction: column;
  }
}

.inp > * {
  cursor: pointer;
}

.container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.container {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 20px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  text-shadow: 0px 0px 2px white;
  @media only screen and (max-width: 1400px) {
    &:first-child {
      margin-left: 0;
    }
    margin-left: 40px;
  }

  @media only screen and (max-width: 380px) {
    margin-left: 0;
  }
}

/* Create a custom radio button */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 20px;
  background-color: #eee;
  border-radius: 50%;
  box-shadow: 0px 0px 15px 1px black;
}

/* On mouse-over, add a grey background color */
.container:hover input ~ .checkmark {
  background-color: #ccc;
}

/* When the radio button is checked, add a blue background */
.container input:checked ~ .checkmark {
  background-image: radial-gradient(#f19655, #5e4028);
  filter: url("#noise2");
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
.container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */
.container .checkmark:after {
  top: 6.5px;
  left: 6.5px;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: white;
}

.radiob {
  visibility: hidden;
}
